.Auth {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 100px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    background: -webkit-gradient(linear, right top, left top, from(#732875), to(#b859ef));
    background: -webkit-linear-gradient(right, #732875 0%, #b859ef 100%);
    background: -o-linear-gradient(right, #732875 0%, #b859ef 100%);
    background: linear-gradient(270deg, #732875 0%, #b859ef 100%);
}

.Auth>div {
    width: 100%;
    max-width: 600px;
    padding: 0 20px;
}

.Auth h1 {
    color: #fff;
    text-align: center
}

.AuthForm {
    background: rgb(212, 185, 210);
    -webkit-box-shadow: 2px 2px 2px rgba(0,0,0, .5);
            box-shadow: 2px 2px 2px rgba(0,0,0, .5);
    padding: 15px;
    border-radius: 5px;
}


