.Quiz {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 100px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    background: -webkit-gradient(linear, left top, right top, from(#c5d346), to(#aca035));
    background: -webkit-linear-gradient(left, #c5d346 0%, #aca035 100%);
    background: -o-linear-gradient(left, #c5d346 0%, #aca035 100%);
    background: linear-gradient(90deg, #c5d346 0%, #aca035 100%);

}

.Quiz h1 {
    color: #fff;
    margin-left: 10px;
}

.QuizWrapper {
    width: 600px;
}




