.QuizCreator {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 50px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    background: -webkit-gradient(linear, right top, left top, from(#1736cf), to(#9c8f74));
    background: -webkit-linear-gradient(right, #1736cf 0%, #9c8f74 100%);
    background: -o-linear-gradient(right, #1736cf 0%, #9c8f74 100%);
    background: linear-gradient(270deg, #1736cf 0%, #9c8f74 100%);
}

.QuizCreator > div {
    width: 100%;
    max-width: 750px;
    padding: 0 20px;
}

.QuizCreator h1 {
    text-align: center;
    color: white
}

.QuizCreator form {
    background: rgb(154, 186, 212);
    border-radius: 5px;
    padding: 20px;
    -webkit-box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);
            box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);
    margin-bottom: 40px;
}

.QuizCreator button {
    margin-top: 10px
}


