.QuizList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 100px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    background: -webkit-gradient(linear, left top, right top, from(#55bafd), color-stop(37%, #578cf0), to(#f7e29c));
    background: -webkit-linear-gradient(left, #55bafd 0%, #578cf0 37%, #f7e29c 100%);
    background: -o-linear-gradient(left, #55bafd 0%, #578cf0 37%, #f7e29c 100%);
    background: linear-gradient(90deg, #55bafd 0%, #578cf0 37%, #f7e29c 100%);
}

.QuizList h1 {
    color: #ffffff;
}

.QuizList ul {
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: center;
}

.QuizList li {
    margin-bottom: 10px;
}

.QuizList li a {
    text-decoration: none;
    color: #fff;
}

.QuizList li a:hover {
    color: yellow
}


